import {
        mapState
    } from 'vuex';
	import uParse from '@/components/u-parse/u-parse.vue'
import menus from "../../components/menu.vue"
	import interfaces from '../../utils/interfaces.js';
export default {
	computed: {
	            ...mapState(['type'])
	        },
	components:{
		menus,
		uParse
	},
	data() {
		return {
			jianjie:"",
			jianjieE:"",
			swiperList:[],
			video:""
		}
	},
	onLoad() {
		this.getData();
		this.getImg();
	},
	methods: {
		onPullDownRefresh() {
		        //监听下拉刷新动作的执行方法，每次手动下拉刷新都会执行一次
				this.getData();
				this.getImg();
				
				 uni.stopPullDownRefresh();
				
		    },
		getImg(){
		this.request({
			url: interfaces.getvideo,
			success: ((res) => {
				if(res.errno==0){
					if(res.data!=null){
					this.video=res.data.gUrl;	
					}
					
					
					/* for(let i=0;i<this.swiperList.length;i++){
						if(this.swiperList[i].gType=="video"){
							this.video=this.swiperList[i];
						}
						
					
					} */
				}
				
			
		
			})
		})
		},
		
		getData(){
	
			this.request({
				url: interfaces.getIntroduce,
				success: ((res) => {
					if(res.errno==0){
						this.jianjie=res.data.introduce;
						this.jianjieE=res.data.eintroduce;
					}
					//console.log(res);
					
				
			
				})
			})
		},
		intotech(){
			uni.navigateTo({
				url:"../tech/tech"
			})
		},
		intomu(){
			uni.navigateTo({
				url:"../mu/mu"
			})
		},
		intohonor(){
		uni.navigateTo({
			url:"../honor/honor"
		})
		},
		intocust(){
			uni.navigateTo({
				url:"../customer/customer"
			})
		}
	}
}
